*{box-sizing:border-box}html,body{font-family:"helvetica neue", "roboto", "sans-serif"}h1{font-weight:100;margin:0.25rem 0}h2{font-weight:100;margin:0.25rem 0}h3{font-size:24px;font-weight:100}h5{font-size:12px;color:#555;font-weight:normal;text-transform:uppercase;margin:.5em 0;padding-left:0;padding-right:0}.row{display:flex;flex-direction:row}.row .column{flex-basis:1;width:50%;height:100%;padding:8px}hr{background:transparent;border:none;border-top:1px solid rgba(0,0,0,0.2);margin-top:1rem;margin-bottom:1rem}button{background:#f9f9f9;background:#eee;padding:8px;border:none;border-radius:3px;font-weight:bold;box-shadow:0 1px 16px -4px rgba(0,0,0,0.2);color:#333;cursor:pointer;outline:none;transition:ease-out .3s box-shadow, ease-out .3s transform}@media (hover: hover){button:hover{background:#fff;box-shadow:0 1px 8px 2px rgba(0,0,0,0.3);transform:translateY(-1px)}}button+button{margin-left:8px}button.delete{background:#dd1d1d;color:#fff}button.delete:hover{background:#e54a4a}button.edit{background:#d4d4d4}button.confirm{background:#3dcfb4;color:#fff}input[type="text"]{width:100%;padding:4px;font-size:14px;border-radius:2px;border:0px solid #ccc}.loading{min-height:100px}.loading:after{width:100%;height:50px;margin-top:25px;margin-bottom:25px;content:"Loading....";font-style:italic;color:#777}.message{position:fixed;width:100%;height:60px;bottom:0;left:0;background:#fff;color:#000;padding:16px;font-size:16px}.message.error{background:#d92a2a;color:#fff}.message.success{background:#89d828;color:#fff}@media (max-width: 1224px){html,body{font-size:12px}}@keyframes rotateMe{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}.Products{display:flex;flex-direction:row;flex-wrap:wrap}.Products .column{flex-basis:1;width:50%;height:100%;padding:24px}.Products .imageList{display:flex;flex-direction:column;align-items:flex-start;justify-content:flex-start;width:100%}.Products .imageList .listItem{display:flex;flex-direction:row;align-items:flex-start;justify-content:flex-start}.Products .imageList .listItem .preview{flex-basis:3rem;height:3rem;flex-shrink:1;flex-grow:0;overflow:hidden}.Products .imageList .listItem .preview>div>div{width:3rem !important;height:3rem !important}.Products .imageList .listItem .preview>div>div>div{width:3rem !important;height:3rem !important}.Products .imageList .listItem .content{flex-basis:100%;height:40px;flex-shrink:1;flex-grow:1;padding:2px}.Products .imageList .listItem .content h5{padding:0;margin:0}.Products .imageList .listItem .content h6{font-size:11px;padding:2px 0;margin:0;font-weight:normal;color:#777}.Products .imageList .listItem .meta{flex-basis:50%;flex-shrink:1;flex-grow:1;height:3rem;line-height:3rem}.Products .imageList .imageCard{width:100%;height:3rem;background:#f9f9f9;margin:1px 0;font-size:11px;position:relative}.Products .imageList .imageCard button{position:relative;background:transparent;box-shadow:none}.Products .imageList .imageCard button.editButton{position:absolute;top:4px;right:4px;opacity:1}.Products .imageList .imageCard .theCount{width:18px;height:18px;background:#222;text-align:center;line-height:18px;font-size:12px;color:#eee;border-radius:50%;display:inline-block;vertical-align:middle}



