*{box-sizing:border-box}html,body{font-family:"helvetica neue", "roboto", "sans-serif"}h1{font-weight:100;margin:0.25rem 0}h2{font-weight:100;margin:0.25rem 0}h3{font-size:24px;font-weight:100}h5{font-size:12px;color:#555;font-weight:normal;text-transform:uppercase;margin:.5em 0;padding-left:0;padding-right:0}.row{display:flex;flex-direction:row}.row .column{flex-basis:1;width:50%;height:100%;padding:8px}hr{background:transparent;border:none;border-top:1px solid rgba(0,0,0,0.2);margin-top:1rem;margin-bottom:1rem}button{background:#f9f9f9;background:#eee;padding:8px;border:none;border-radius:3px;font-weight:bold;box-shadow:0 1px 16px -4px rgba(0,0,0,0.2);color:#333;cursor:pointer;outline:none;transition:ease-out .3s box-shadow, ease-out .3s transform}@media (hover: hover){button:hover{background:#fff;box-shadow:0 1px 8px 2px rgba(0,0,0,0.3);transform:translateY(-1px)}}button+button{margin-left:8px}button.delete{background:#dd1d1d;color:#fff}button.delete:hover{background:#e54a4a}button.edit{background:#d4d4d4}button.confirm{background:#3dcfb4;color:#fff}input[type="text"]{width:100%;padding:4px;font-size:14px;border-radius:2px;border:0px solid #ccc}.loading{min-height:100px}.loading:after{width:100%;height:50px;margin-top:25px;margin-bottom:25px;content:"Loading....";font-style:italic;color:#777}.message{position:fixed;width:100%;height:60px;bottom:0;left:0;background:#fff;color:#000;padding:16px;font-size:16px}.message.error{background:#d92a2a;color:#fff}.message.success{background:#89d828;color:#fff}@media (max-width: 1224px){html,body{font-size:12px}}@keyframes rotateMe{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}.SwatchGroup{border:1px solid #ccc;padding:4px;border-radius:4px;background:#fff;box-shadow:0 1px 8px -2px rgba(0,0,0,0.2);flex-wrap:nowrap;position:relative}.SwatchGroup:hover{cursor:pointer}.SwatchGroup.horizontal{flex-direction:row;display:flex;align-items:flex-start;max-height:160px;min-width:300px;width:100%}.SwatchGroup.horizontal .segment{display:flex;flex:1 1 auto;flex-basis:50px;min-width:50px;height:100%;flex-basis:50px;min-height:50px;background:#eee}.SwatchGroup.horizontal .segment:nth-child(2){border-radius:3px 0 0 3px}.SwatchGroup.horizontal .segment:last-child{border-radius:0 3px 3px 0}.SwatchGroup.vertical{flex-direction:column-reverse;align-items:stretch;min-height:300px;max-width:160px}.SwatchGroup.vertical .segment{display:flex;width:100%;flex:1 1 auto;flex-basis:50px;min-height:50px;background:#eee}.SwatchGroup.vertical .segment:nth-child(2){background:red !important;border-radius:3px 3px 0 0}.SwatchGroup.vertical .segment:last-child{border-radius:0 0 3px 3px}.SwatchGroup .segment{position:relative;overflow:hidden}.SwatchGroup .segment.info{background:#fff;padding:4px;flex-basis:25px}.SwatchGroup .segment.info h4{font-size:11px;margin:0}.SwatchGroup .segment .colorSwatch{width:100%;height:100%;display:block;position:absolute}.SwatchGroup .segment .colorName{position:absolute;left:8px;top:8px;font-size:11px;color:#fff;display:none;text-shadow:0px 0px 2px #000;font-weight:bold}.SwatchGroup .segment:hover .colorName{display:block}.SwatchGroup.isFav{border:1px solid #7b7b7b}.SwatchGroup.isFav:before{content:"•";width:1rem;height:1rem;background:#45c00b;border-radius:1rem;bottom:.25rem;left:.25rem;z-index:99;display:flex;align-items:center;justify-content:center;line-height:1rem;color:#fff;position:absolute}



