,0.2)}.ImageAdmin .listItem.isActive{background:#fff}.ImageAdmin .listItem .thumbnail{height:30px}.ImageAdmin .svgItem{width:100%;padding:2px;background:#fff;margin-top:1px;display:flex;min-height:2rem;align-items:center;justify-content:stretch;flex-wrap:wrap}.ImageAdmin .svgItem.isSelected{background:lime}.ImageAdmin .svgItem.toDelete{background:#fff}.ImageAdmin .svgItem.toDelete .buttonWrap{opacity:0;visibility:0}.ImageAdmin .svgItem>div{flex-basis:38%;flex-shrink:1;flex-grow:1}.ImageAdmin .svgItem .svgIcon{width:1.25rem;height:100%;background:#f3f3f3;border-radius:3px;margin:1px;margin-right:1ch;height:1.5rem;display:inline-block;vertical-align:middle;color:#ccc;text-align:center;line-height:1.5rem;font-size:11px;flex-basis:1.25rem;flex-grow:0}.ImageAdmin .svgItem .buttonWrap{flex-basis:10%;flex-grow:1}.ImageAdmin .svgItem .deleteWarning{background:#ff1934;color:#fff;display:flex;align-items:center;justify-content:space-between;border-radius:4px;flex-basis:98%;flex-shrink:0;line-height:2rem;padding:1rem;margin:.5rem}.ImageAdmin .theTick{vertical-align:middle;display:none;width:40px;height:40px;position:absolute;top:-4px;left:-8px}.ImageAdmin .theTick svg{position:absolute;left:0;top:0;width:100%;height:100%}.ImageAdmin .theTick svg g path{fill:rgba(0,0,0,0.1)}



