*{box-sizing:border-box}html,body{font-family:"helvetica neue", "roboto", "sans-serif"}h1{font-weight:100;margin:0.25rem 0}h2{font-weight:100;margin:0.25rem 0}h3{font-size:24px;font-weight:100}h5{font-size:12px;color:#555;font-weight:normal;text-transform:uppercase;margin:.5em 0;padding-left:0;padding-right:0}.row{display:flex;flex-direction:row}.row .column{flex-basis:1;width:50%;height:100%;padding:8px}hr{background:transparent;border:none;border-top:1px solid rgba(0,0,0,0.2);margin-top:1rem;margin-bottom:1rem}button{background:#f9f9f9;background:#eee;padding:8px;border:none;border-radius:3px;font-weight:bold;box-shadow:0 1px 16px -4px rgba(0,0,0,0.2);color:#333;cursor:pointer;outline:none;transition:ease-out .3s box-shadow, ease-out .3s transform}@media (hover: hover){button:hover{background:#fff;box-shadow:0 1px 8px 2px rgba(0,0,0,0.3);transform:translateY(-1px)}}button+button{margin-left:8px}button.delete{background:#dd1d1d;color:#fff}button.delete:hover{background:#e54a4a}button.edit{background:#d4d4d4}button.confirm{background:#3dcfb4;color:#fff}input[type="text"]{width:100%;padding:4px;font-size:14px;border-radius:2px;border:0px solid #ccc}.loading{min-height:100px}.loading:after{width:100%;height:50px;margin-top:25px;margin-bottom:25px;content:"Loading....";font-style:italic;color:#777}.message{position:fixed;width:100%;height:60px;bottom:0;left:0;background:#fff;color:#000;padding:16px;font-size:16px}.message.error{background:#d92a2a;color:#fff}.message.success{background:#89d828;color:#fff}@media (max-width: 1224px){html,body{font-size:12px}}@keyframes rotateMe{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}.RenderImage{position:relative;width:100%;background:#f9f9f9;padding:8px;box-shadow:0 1px 0px rgba(0,0,0,0.1);margin-top:1px;border-radius:8px}.RenderImage .column p{margin:.25rem 0;display:flex;align-items:center;justify-content:flex-start}.RenderImage .column p span{font-weight:bold}.RenderImage .column p span:first-child{font-weight:normal;flex-basis:100px}.RenderImage .infoRow h4{font-size:12px;line-height:12px;margin:4px 0}.RenderImage .row.invisible{overflow:hidden;height:1px;position:relative}.RenderImage .theTick{width:40px;height:40px;position:relative;display:inline-block;vertical-align:middle;line-height:1em}.RenderImage .theTick svg{position:absolute;left:0;top:0;width:100%;height:100%}.RenderImage .theTick svg g path{fill:#73d030}.RenderImage .titleBlock{display:block;position:relative;border-top:1px dotted #ccc}.RenderImage .titleBlock button{position:absolute;right:0;top:4px}.RenderImage .toggleWrap{background:#fff;opacity:0;visibility:hidden;height:1px}.RenderImage .toggleWrap.visible{opacity:1;visibility:visible;height:inherit}.RenderImage .cropSource{position:absolute;width:50px;height:50px;overflow:hidden}.RenderImage .renderImg{vertical-align:top;position:relative}.RenderImage .renderImg img{max-width:100%}.RenderImage .renderImg h4{color:#ccc;margin:0}.RenderImage .renderImg .placeholder{min-height:170px;background:#f3f3f3;width:auto;font-size:11px;font-style:italic;text-align:center;line-height:150px;color:#777}.RenderImage .renderImg .placeholder.rendering,.RenderImage .renderImg .placeholder.saving{position:absolute;left:0;top:0;width:100%;height:100%}.RenderImage .renderImg.done h4{color:#222}.RenderImage .renderImg.done .theTick svg g path{fill:#AFD621}.RenderImage .theLoading{width:20px !important;height:20px;position:relative;display:inline-block;line-height:2em;vertical-align:middle;margin-right:6px;animation-duration:1s;animation-name:rotateMe;animation-timing-function:linear;animation-iteration-count:infinite}.RenderImage .theLoading svg{position:absolute;left:0;top:0;width:100%;height:100%}.RenderImage .theLoading svg g path{fill:rgba(0,0,0,0.5)}.RenderImage .detailRow.visible{border-top:1px dashed #ccc;min-height:64px}.RenderImage .detailRow.visible p{font-size:12px}



