.App{height:calc(100% - 48px);width:100%;background:#ddd;padding:0;font-family:sans-serif;position:fixed;left:0;top:48px;bottom:0;right:0;overflow:scroll;-webkit-overflow-scrolling:touch}.App .header{position:absolute;width:100%;height:48px;left:0;top:0;background:#fff;width:100%;height:48px;left:0;top:0;background:#292929;box-shadow:0 2px 28px -6px #000;position:fixed;z-index:9999}.App .header .headerItem{display:inline-block;height:48px;vertical-align:middle;line-height:32px;padding:8px;text-transform:uppercase;color:#888;text-decoration:none;letter-spacing:.2ch;font-size:.835rem;border-left:1px solid rgba(255,255,255,0.1)}.App .header .headerItem:last-child{border-right:1px solid rgba(255,255,255,0.1)}.App .header .headerItem.isActive{color:#fff;background:#000}



