*{box-sizing:border-box}html,body{font-family:"helvetica neue", "roboto", "sans-serif"}h1{font-weight:100;margin:0.25rem 0}h2{font-weight:100;margin:0.25rem 0}h3{font-size:24px;font-weight:100}h5{font-size:12px;color:#555;font-weight:normal;text-transform:uppercase;margin:.5em 0;padding-left:0;padding-right:0}.row{display:flex;flex-direction:row}.row .column{flex-basis:1;width:50%;height:100%;padding:8px}hr{background:transparent;border:none;border-top:1px solid rgba(0,0,0,0.2);margin-top:1rem;margin-bottom:1rem}button{background:#f9f9f9;background:#eee;padding:8px;border:none;border-radius:3px;font-weight:bold;box-shadow:0 1px 16px -4px rgba(0,0,0,0.2);color:#333;cursor:pointer;outline:none;transition:ease-out .3s box-shadow, ease-out .3s transform}@media (hover: hover){button:hover{background:#fff;box-shadow:0 1px 8px 2px rgba(0,0,0,0.3);transform:translateY(-1px)}}button+button{margin-left:8px}button.delete{background:#dd1d1d;color:#fff}button.delete:hover{background:#e54a4a}button.edit{background:#d4d4d4}button.confirm{background:#3dcfb4;color:#fff}input[type="text"]{width:100%;padding:4px;font-size:14px;border-radius:2px;border:0px solid #ccc}.loading{min-height:100px}.loading:after{width:100%;height:50px;margin-top:25px;margin-bottom:25px;content:"Loading....";font-style:italic;color:#777}.message{position:fixed;width:100%;height:60px;bottom:0;left:0;background:#fff;color:#000;padding:16px;font-size:16px}.message.error{background:#d92a2a;color:#fff}.message.success{background:#89d828;color:#fff}@media (max-width: 1224px){html,body{font-size:12px}}@keyframes rotateMe{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}.Upload .theLoading{width:20px !important;height:20px;position:relative;display:inline-block;line-height:2em;vertical-align:middle;margin-right:6px;animation-duration:1s;animation-name:rotateMe;animation-timing-function:linear;animation-iteration-count:infinite}.Upload .theLoading svg{position:absolute;left:0;top:0;width:100%;height:100%}.Upload .theLoading svg g path{fill:rgba(0,0,0,0.5)}.Upload .dropZone{border:2px dashed #ccc;background-color:rgba(0,0,0,0.065);display:flex;align-items:center;justify-content:center;border-radius:.5rem;will-change:background-color, border-color;transition:.3s background-color ease-in-out, .3s border-color ease-in-out, .3s color ease-in-out;height:inherit;width:100%;line-height:1.25rem;padding:.25rem;min-height:70vh}.Upload .dropZone span{margin-bottom:0;display:flex;align-items:center;justify-content:center;font-style:italic;color:#777}.Upload .dropZone .iconWrap{margin-right:1rem;transform:scale(1);transition:.3s transform ease-in-out}.Upload .dropZone:hover{background-color:rgba(255,255,255,0.5)}.Upload .dropZone.dropZoneActive{border:2px dashed #333;background-color:#fff;color:#333}.Upload .dropZone.dropZoneReject{border:2px dashed red;background-color:rgba(255,0,0,0.1);color:red}@keyframes rotateMe{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}



